:root {
  --purple-color: #b68dff;
  --blue-color: #34dbff;
  --cyan-color: #30cff1;
  --yellow-color: #ffdb22;
  --green-color: #80de5f;
  --pink-color: #ff9bcb;
  --white-color: #fff;
  --black-color: #000;
  --black-2-color: #323035;
  --gray-middle-color: #a8a8a8;
  --dark-gray: #919fa9;
  --deep-blue-color: #170586;
  --gray-color: #898989;
  --gray-2-color: #5b5b5b;
  --gray-3-color: #b3b3b3;
  --gray-4-color: #7d7d7d;
  --light-gray: #e2e6e9;
  --red-color: #ff4141;
  --silver-color: #f8f8f8;
  --gray-thin-color: #f1f1f1;
  --gray-thick-color: #f5f5f5;
  --silver-thin-color: #fdfdfd;
  --aqua-color: #34bdff;
  --header-1: 400 90px / 1 "ALS Haus";
  --header-2: 400 64px / 68px "ALS Haus";
  --header-3: 400 44px / 56px "ALS Haus";
  --header-4: 500 24px / 31.2px "ALS Haus";
  --caption: 400 24px / 31.87px "ALS Haus";
  --paragraph: 400 20px / 28px "Inter";
  --landing-width: 1360px;
}

@font-face {
  font-display: swap;
  font-family: Inter;
  src: url("Inter-Regular.b298cd52.ttf");
}

@font-face {
  font-display: swap;
  font-family: Lato;
  src: url("Lato-Regular.ad872c70.ttf");
}

@font-face {
  font-display: swap;
  font-family: ALS Haus;
  src: url("ALS_Hauss-Regular.8fea8b3e.otf");
}

html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

body, h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
}

.page {
  overflow-x: hidden;
}

.content {
  width: 100%;
  text-overflow: ellipsis;
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1360px;
  color: var(--black-color);
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin: 0 auto;
  display: flex;
  position: relative;
}

.pageBlock {
  max-width: var(--landing-width);
  margin: 0 auto;
}

.primaryBtn {
  color: var(--white-color);
  background-color: var(--black-color);
  text-align: center;
  cursor: pointer;
  border-width: 0;
  border-radius: 200px;
  justify-content: center;
  align-items: center;
  transition: background-color .75s;
  display: flex;
}

.primaryBtn-large {
  width: 216px;
  padding: 24px 32px;
  font: 600 24px / 29.05px Inter;
}

.primaryBtn-medium {
  width: 175px;
  padding: 20px 32px;
  font: 600 20px / 24.2px Inter;
}

.primaryBtn-small {
  width: 151px;
  padding: 10px 20px;
  font: 400 16px / 21.25px ALS Haus;
}

.primaryBtn-disabled {
  background-color: var(--gray-thin-color);
  color: var(--dark-gray);
}

.primaryBtn:hover {
  background-color: var(--black-2-color);
}

.secondaryBtn {
  color: var(--black-color);
  background-color: var(--gray-thin-color);
  letter-spacing: 0;
  text-align: left;
  text-align: center;
  cursor: pointer;
  border-width: 0;
  border-radius: 200px;
  justify-content: center;
  align-items: center;
  padding: 20px 32px;
  font-family: Inter;
  font-size: 20px;
  font-weight: 600;
  line-height: 24px;
  display: flex;
}

.secondaryBtn:hover, .secondaryBtn:active {
  color: var(--white-color);
  background-color: var(--purple-color);
}

.form-checkbox {
  width: 24px;
  height: 24px;
  border: 1px solid var(--light-gray);
  user-select: none;
  border-radius: 6px;
  margin-right: 12px;
  display: inline-flex;
}

.form-checkbox__input {
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
  position: absolute;
}

.link {
  color: var(--black-color);
  cursor: pointer;
  font: 400 16px / 21.25px sans-serif;
  text-decoration: none;
}

.link:hover, .link:active {
  color: var(--aqua-color);
}

.form-checkbox:hover, .form-checkbox:focus {
  border-color: var(--purple-color);
}

.form__checkbox-label input:checked ~ .form-checkbox {
  border-color: var(--purple-color);
  background-image: url("checkbox.2899f9d3.svg");
  border: none;
}

.countrySelector {
  z-index: 5;
  background-color: var(--gray-thin-color);
  letter-spacing: 0;
  text-align: left;
  max-width: fit-content;
  white-space: nowrap;
  border-radius: 190px;
  align-items: center;
  padding: 16px;
  font-family: Inter;
  font-size: 16px;
  font-weight: 600;
  line-height: 19px;
  display: flex;
  position: absolute;
  top: 32px;
  left: 32px;
}

.flagIcon {
  width: 24px;
  height: 24px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: center;
  align-items: center;
  margin-right: 12px;
  display: flex;
}

.counrySelector__option {
  align-items: center;
  display: flex;
}

.flagIcon-USA {
  background-image: url("USAflag.6191b306.svg");
}

.flagIcon-Canada {
  background-image: url("Canadaflag.4dbd66a4.svg");
}

.flagIcon-Australia {
  background-image: url("Australiaflag.0895dcc1.svg");
}

.formInput {
  width: 422px;
  border: 1px solid var(--light-gray);
  letter-spacing: 0;
  text-align: left;
  box-shadow: none;
  height: 59px;
  border-radius: 16px;
  padding: 20px;
  font-family: Inter;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
}

.modal__description {
  color: var(--gray-2-color);
  text-align: center;
}

.formInput:hover, .formInput:focus {
  box-shadow: 0 0 0 2px var(--purple-color);
  outline: none;
}

.formInput:invalid {
  box-shadow: 0 0 0 2px var(--red-color);
  outline: none;
}

.cookies {
  width: 330px;
  background-color: var(--white-color);
  z-index: 11;
  border-radius: 120px;
  justify-content: space-between;
  align-items: center;
  padding: 20px 20px 20px 32px;
  font-family: ALS Haus;
  display: flex;
  position: fixed;
  bottom: 40px;
  left: calc(50% - 165px);
}

.cookies__text {
  letter-spacing: 0;
  text-align: left;
  color: var(--black-color);
  font-size: 20px;
  font-weight: 400;
  line-height: 27px;
}

.cookies__btn {
  background-color: var(--black-color);
  color: var(--white-color);
  letter-spacing: 0;
  cursor: pointer;
  border-radius: 80px;
  padding: 12px 20px 8px;
  font-family: ALS Haus;
  font-size: 16px;
  line-height: 21px;
}

.modal, .modal-scrollable {
  z-index: 100;
  background-color: #000000b3;
  display: none;
  position: fixed;
  inset: 0;
}

.modal-opened {
  justify-content: center;
  align-items: center;
  display: flex;
}

.modal__closeBtn {
  width: 26px;
  height: 26px;
  cursor: pointer;
  user-select: none;
  display: flex;
  position: fixed;
  top: 46px;
  right: 46px;
}

.modal__form {
  background-color: var(--white-color);
  border-radius: 30px;
  padding: 44px;
  display: none;
}

.modal__form-show {
  flex-direction: column;
  display: flex;
}

.form {
  flex-direction: column;
  gap: 24px;
  display: flex;
}

.line {
  width: 35px;
  height: 2px;
  background-color: var(--white-color);
  position: absolute;
  transform: translate(-5px, 12px)rotate(-45deg);
}

.modal__closeBtn:after {
  content: "";
  width: 35px;
  height: 2px;
  background-color: var(--white-color);
  position: absolute;
  transform: translate(-5px, 12px)rotate(45deg);
}

.title {
  letter-spacing: 0;
  text-align: left;
  font-family: ALS Haus;
  font-size: 64px;
  font-weight: 400;
  line-height: 68px;
}

.description {
  letter-spacing: 0;
  text-align: left;
  font-family: Inter;
  font-size: 20px;
  font-weight: 400;
  line-height: 28px;
}

.description-gray {
  color: var(--gray-2-color);
}

.headerContainer {
  width: 100vw;
  background-color: var(--purple-color);
  min-height: 83px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.header {
  width: 100%;
  max-width: 1280px;
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

.header__link-mobile {
  font-family: ALS Haus;
}

.loginBlock-btn {
  letter-spacing: 0;
  text-align: left;
  font-family: Inter;
  font-size: 20px;
  font-weight: 600;
  line-height: 24px;
}

.header__navigation__logo {
  padding-right: 8px;
}

.header__navigation {
  align-items: center;
  gap: 24px;
  display: flex;
}

.header__navigation-mobile {
  transition: all .5s;
  display: none;
}

.header__logo {
  padding-right: 20px;
}

.header__navlist-closed {
  display: none;
  transform: translateY(-700px);
}

.header__navlist-opened {
  height: 600px;
  flex-direction: column;
  padding: 0 20px;
  display: flex;
  transform: translateY(0);
}

.menu__mobile {
  width: 20px;
  height: 26px;
  background-color: var(--purple-color);
  border: none;
  margin: 0;
  padding: 0;
}

.menu__icon, .menu__mobile:before, .menu__mobile:after {
  background: var(--black-color);
  content: "";
  height: 2px;
  border-radius: 10px;
  margin: 5px 0;
  transition: all .5s;
  display: block;
}

.menu__mobile-opened .menu__icon {
  transform: scale(0);
}

.menu__mobile-opened:before {
  transform: translateY(8px)rotate(135deg);
}

.menu__mobile-opened:after {
  transform: translateY(-6px)rotate(-135deg);
}

.header__buttonBlock-mobile {
  align-items: center;
  gap: 20px;
  display: flex;
}

.header__links-mobile {
  flex-direction: column;
  gap: 24px;
  margin-top: 42px;
  display: flex;
}

.header__link-mobile {
  letter-spacing: 0;
  color: var(--white-color);
  font-size: 44px;
  line-height: 58px;
  text-decoration: none;
}

.header__loginBlock {
  justify-content: space-between;
  align-items: center;
  gap: 24px;
  display: flex;
}

.header__loginBlock-mobile {
  flex-direction: column;
  gap: 24px;
  margin-top: 153px;
  display: flex;
}

